<template>
  <div
    id="scroll"
    v-if="currentInfo"
  >
    <div
      v-if="currentInfo.roomScale && currentInfo.virtualRoomScale"
      class="flex"
    >
      <div class="w30">房型名称</div>
      <div class="w70">{{ currentInfo.roomScale.scaleName + '-' + currentInfo.virtualRoomScale.scaleName }}</div>
    </div>
    <div
      v-if="currentInfo.order"
      class="flex"
    >
      <div class="w30">房间号</div>
      <div class="w70">{{ currentInfo.roomSn }}</div>
    </div>
    <div
      v-if="currentInfo.order && orderSourceMap"
      class="flex"
    >
      <div class="w30">用户来源</div>
      <div class="w70">{{ orderSourceMap[currentInfo.order.orderSource] }}</div>
    </div>
    <div
      v-if="currentInfo.order"
      class="flex"
    >
      <div class="w30">订单号</div>
      <div class="w70">{{ currentInfo.order.orderSn }}</div>
    </div>
    <div class="flex">
      <div class="w30">入离日期</div>
      <div class="w70">{{ time }}</div>
    </div>
    <div
      v-if="currentInfo.order"
      class="flex"
    >
      <div class="w30">入住人</div>
      <div class="w70">{{ currentInfo.order.roomUser?currentInfo.order.roomUser.replace(/^(.).+(.)$/g, "$1*$2"):'' }}</div>
    </div>
    <div
      class="flex"
      style="margin-top:10px;font-weight:bold;"
      @click="inCome = !inCome"
    >
      <div
        class="w30"
        style="color:#000;"
      >房费收入</div>
      <div
        class="w70"
        style="color:#57D092;"
      >￥{{ inComeTotel }}</div>
      <van-icon :name="inCome == false?'arrow':'arrow-down'" />
    </div>
    <div v-if="inCome">
      <div class="flex">
        <div class="w30">房费金额</div>
        <div class="w70">￥{{ currentInfo.roomRealAmount }}</div>
      </div>
      <div class="flex">
        <div class="w30">延时退房扣押</div>
        <div class="w70">￥{{ currentInfo.autoDepositCut }}</div>
      </div>
      <div class="flex">
        <div class="w30">平台佣金</div>
        <div class="w70">-￥{{ currentInfo.commission }}</div>
      </div>
      <div class="flex">
        <div class="w30">房费优惠</div>
        <div class="w70">-￥{{ currentInfo.refuseLandLord }}</div>
      </div>
      <div class="flex">
        <div class="w30">房费冲账</div>
        <div class="w70">-￥{{ currentInfo.reverseAmount }}</div>
      </div>
    </div>
    <div
      class="flex"
      style="margin-top:10px;color:#000;font-weight:bold;"
      @click="outCome = !outCome"
    >
      <div
        class="w30"
        style="color:#000;"
      >运营支出</div>
      <div
        class="w70"
        style="color:#FF5257;"
      >-￥{{ outComeTotel }}</div>
      <van-icon :name="outCome == false?'arrow':'arrow-down'" />
    </div>
    <div v-if="outCome">
      <div class="flex">
        <div class="w30">运营服务费</div>
        <div class="w70">￥{{ currentInfo.maAmount || 0 }}</div>
      </div>
      <div class="flex">
        <div class="w30">保洁人工费</div>
        <div class="w70">￥{{ currentInfo.cleanAmount }}</div>
      </div>
      <div class="flex">
        <div class="w30">日常消耗品</div>
        <div class="w70">￥{{ currentInfo.cleanConAmount || 0 }}</div>
      </div>
      <div class="flex">
        <div class="w30">日常洗涤费用</div>
        <div class="w70">￥{{ currentInfo.cleanWashAmount || 0 }}</div>
      </div>
    </div>
    <div
      class="flex"
      style="margin-top:10px;font-weight:bold;"
    >
      <div
        class="w30"
        style='color:#000;'
      >结算金额</div>
      <div class="w70 f7">{{ Math.abs(inComeTotel) == Math.abs(outComeTotel) ? '':Math.abs(inComeTotel) > Math.abs(outComeTotel)?'+':'-' }}￥
        {{ (Math.abs(inComeTotel - outComeTotel)).toFixed(2) }}</div>
    </div>
  </div>
</template>
<script>
import '@/assets/styles/global.css';
import { GetSystemDictDataByType } from '@/api/api_query';
import { GetBusinessOrderroomSumBill } from '@/api/api_business_hotel_and_scale'
import dayjs from 'dayjs';
export default {
    data() {
        return {
            orderSourceMap: {},
            currentInfo: {},
            scaleName: null,
            time: null,
            roomRealAmount: null,
            refuseLandLord: null,
            reverseAmount: null,
            accountAmount: null,
            inCome: false,
            outCome: false,
            inComeTotel: 0,
            outComeTotel: 0
        };
    },
    created() {
        this.$store.commit('setHeader', '结算详情-暂关');
        this.getCurrent();
    },
    methods: {
        // 订单详情
        getCurrent() {
            GetBusinessOrderroomSumBill(this.$route.query.orderRoomId).done((res) => {
                this.currentInfo = res.data;
                this.time = dayjs(this.currentInfo.order.startTime).format('YYYY-MM-DD') + '至' + dayjs(this.currentInfo.order.endTime).format('YYYY-MM-DD') + '  ' + '共' + this.currentInfo.dayIn + '晚';
                this.inComeTotel = this.currentInfo.roomRealAmount + this.currentInfo.autoDepositCut - this.currentInfo.commission - this.currentInfo.refuseLandLord - this.currentInfo.reverseAmount;
                this.outComeTotel = this.currentInfo.maAmount + this.currentInfo.cleanAmount + this.currentInfo.cleanConAmount + this.currentInfo.cleanWashAmount;
                this.getDay();
            });
        },
        // 支付方式
        getDay() {
            GetSystemDictDataByType({ dictType: 'business_order_source' }).done((res) => {
                let obj = {};
                for (var i = 0; i < res.data.length; i++) {
                    obj[res.data[i].dictValue] = res.data[i].dictLabel;
                }
                this.orderSourceMap = obj;
            });
        }
    }
};
</script>
<style scoped>
#scroll {
    overflow: auto;
    height: 90%;
    margin: 10px 0;
}
.flex {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    justify-content: space-between;
    background: #fff;
    font-size: 15px;
    color: #000;
    border-bottom: 1px solid #efecf0;
    align-items: center;
    padding: 10px 20px;
}
.w30 {
    width: 30%;
    text-align: left;
    color: #8f8d90;
}
.w70 {
    width: 70%;
    text-align: right;
}
.f7 {
    color: #000;
}
</style>



